.container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.container-subsections {
    height: 77vh;
    display: flex;
    width: 80vw;
}

.ad-banner {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.sobre-nos-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
}

.sobre-nos-content {
    width: auto;
    display: flex;
    justify-content: center;
}

#container-home {
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100%;
    box-shadow: 0px 8px 12px rgba(0, 0, 0, 0.1);
    margin-bottom: 50px;
}

#home {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 90vh;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}

.banner {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 500px;
    width: 450px;
    border-radius: 10px;
    background-color: #fff;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
}

.banner h1 {
    border-bottom: 2px solid black;
    text-align: center;
    font-size: 40px;
    width: 90%;
}

.banner p {
    color: #000;
    text-align: center;
    font-size: 20px;
}

.banner-items {
    display: flex;
}

.banner-items i {
    display: flex;
    align-items: center;
    color: #32BBFF;
    font-size: 20px;
    margin-right: 7px;
}

h1 {
    margin-top: 10px;
    margin-bottom: 10px;
}

ltgrey {
    color: #858585;
    font-size: 16px;
}
